import { serviceList } from "@/api/user";

export default {
  name: "CustomerList",
  data() {
    return {
      list: []
    };
  },
  methods: {
    goCustomerService(item) {
      this.$yrouter.push({
        path: "/pages/user/CustomerService/index",
        query: { id: item.uid }
      });
    },
    getList() {
      serviceList().then(res => {
        this.list = res.data;
      });
    }
  },
  mounted() {
    this.getList();
  }
};
